import React from "react"
import Moment from "react-moment"

import Layout from "../components/layout"
import SEO from "../components/seo"

import ResumeData from "../assets/json/resume-data.json"

const educations = ResumeData.education
const certifications = ResumeData.certifications

const EducationPage = () => (
  <Layout>
    <SEO title="Education" />
    <section
      className="resume-section p-3 p-lg-5 d-flex align-items-center animated fadeIn"
      id="education"
    >
      <div className="w-100">
        <h2 className="mb-5">Education</h2>

        <div className="mb-5">
          {educations.map((education, index) => {
            return (
              <div
                className="resume-item d-flex flex-column flex-md-row justify-content-between"
                key={index}
              >
                <div className="resume-content">
                  <h3 className="mb-0">{education.institution}</h3>
                  <div className="subheading mb-3">{education.studyType}</div>
                  <div>{education.area}</div>
                  <p>
                    <Moment format="YYYY">{education.startDate}</Moment> -{" "}
                    <Moment format="YYYY">{education.endDate}</Moment>
                  </p>
                </div>
              </div>
            )
          })}
        </div>

        <h2 className="mb-5">Certifications</h2>

        {certifications.map((certification, index) => {
          return (
            <div
              className="resume-item d-flex flex-column flex-md-row justify-content-between"
              key={index}
            >
              <div className="resume-content">
                <h3 className="mb-0">{certification.name}</h3>
                <div className="subheading mb-3">{certification.type}</div>
                <p>
                  <Moment format="MMMM DD, YYYY">
                    {certification.dateAcquired}
                  </Moment>
                </p>
              </div>
            </div>
          )
        })}
      </div>
    </section>
  </Layout>
)

export default EducationPage
